.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root {
  --mainDark : #343a40;
  --mainGrey : rgba(109, 109, 109);
  --mainLightGrey : rgba(172, 172, 172);
  --mainWhite : #fff;
  --mainBlue : #007bff;
}

.square {
  background:white;
  border: 1px solid gray;
  float: left;
  margin-right:-1px;
  margin-top: -1px;
  padding: 0;
  width: 80px;
  height: 80px;
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  font-weight: bold;
  line-height: 80px;
}

.square:focus {
  outline: none;
}

.bord-row:after {
  clear:both;
  content:"";
  display: table;
}

.game {
  position :absolute;
  top : 50%;
  left :50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  margin : 0 auto;
  
}